import UrlParser from '../../routes/url-parser';
import MakananSource from '../../data/restaurantdb-source';
import { createDaerahDetailTemplate } from '../templates/template-creator';
import LikeButtonInitiator from '../../utils/like-button-initiator';
import FavoriteMakananIdb from '../../data/favorite-resto-idb';
import PostReview from '../../utils/post-review';

const DetailDaerah = {
  async render() {
    return `
      <div id="daerah" class="resto"></div>
      `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const daerah = await MakananSource.daerahDetail(url.daerahId);
    const daerahContainer = document.querySelector('#daerah');

    daerahContainer.innerHTML = createDaerahDetailTemplate(
        daerah.daerah,
      );
    // Fungsi ini akan dipanggil setelah render()
  },
};

export default DetailDaerah;
